<style>:root {
    --aspect-ratio: calc(566/675);
    --logo-height: 45px;
    --logo-width: calc(var(--logo-height)/var(--aspect-ratio));
    --logo-bar-bottom-padding: 0.1rem;
}

.logo {
    height: var(--logo-height);
    width: var(--logo-width);
    margin-right: 0.5rem;
}

.logo,
h2 { display: inline-block; margin:0; }

h2 {
    text-align: center; height:var(--logo-height); vertical-align: middle; width:90%;
}

.logo-bar {
    height: calc(var(--logo-height) + var(--logo-bar-bottom-padding));
    width: 100%;
    padding-bottom: var(--logo-bar-bottom-padding);
}</style>

<script>
    //import Logo from "./brp-logo.png"
    import {sheetName} from '../../../applicationSettings'
    let worksheet_name="";
    const unsubscribe = sheetName.subscribe(value => {
        worksheet_name = value;
    });
</script>
<div class="logo-bar">
<div class="logo">
    <img src="brp-logo.png" height="100%" width="100%">
</div>
<h2>{worksheet_name}</h2>
</div>